$white: #ebebeb;
$mainColor: #282c34;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul, ol {
	list-style-type: none;
	margin: 0;
	padding: 0;
}
a {
	color: inherit;
	text-decoration: none;
	&:hover {
		text-decoration: underline;
	}
}
/*header*/
.App-header {

  nav {

  	ul {
	    display: flex;
	    align-content: center;
	    justify-content: flex-end;
	    list-style-type: none;
	    margin: 0;
	    padding: 20px;
  	}

    a {
    	font-size: 1rem;
      text-decoration: none;
      color: #ebebeb;
      text-shadow: 0 0;

   		padding: 6px 15px;

   		&:hover,
   		&.active {
   			text-decoration: underline;
   		}
    }
  }
}
/* //header*/

.main_btn {
	background: #764676;
	border: none;
	cursor: pointer;
	color: #ebebeb;
	padding: 8px 20px;
	outline: none;

	&:hover {
		background: #a43da4;
	}
}

.txt_content {
	max-width: 800px;
	margin: auto;
	font-size: 1.2rem;
}

.cat_menu {
	display: flex;
	align-content: center;
	justify-content: center;
	max-width: 800px;
	margin: auto;

	& > a {
		padding: 10px 25px;
		margin: 5px;
		font-weight: 600;
		background: $white;
		color: $mainColor;
		display: inline-flex;
		align-items: center;
		justify-content: center;

		&:hover {
			text-decoration: none;
			text-shadow: 0 0;
			background:  lighten($white, 15%);
		}
	}
}

.suctomForm {
	display: flex;
	flex-direction: column;
	max-width: 400px;
	margin: auto;
}
.suctomLabel {
	display: flex;
	align-content: center;
	margin-bottom: 8px;

	span {
		margin-right: 10px;
	}

	input {
		padding: 5px 15px;
		flex: 1;

		&[type='submit']{
			background: #00a000;
			border: none;
			color: #fff;
			padding: 8px 15px;
			cursor: pointer;
		}
	}
}


/*helpers*/
.my-20 {
	margin-top: 20px;
	margin-bottom: 20px;
}